export default {
	data() {
		return {

		}
	},
	methods: {
		// 修改手机号
		moble() {
			uni.navigateTo({
				url: '../editMoble/editMoble'
			})
		},
		// 常见问题
		isSue() {
			uni.navigateTo({
				url: '../issue/issue'
			})
		},
		// 消息通知
		gnews() {
			uni.navigateTo({
				url: '../news/news'
			})
		},
		// 退出登录
		out() {
			uni.showModal({
				title: '提示',
				content: '确定要退出吗?',
				success: function(res) {
					if (res.confirm) {
						uni.clearStorageSync('data');
						uni.reLaunch({
							url: '../login/login'
						})
					}else if(res.cancel){
						uni.showToast({
							title:'取消了退出',
							icon:'none'
						})
					}

				}
			})

		}
	}
}
